@import url(https://fonts.googleapis.com/css?family=B612+Mono|Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  scroll-behavior:smooth;
}

.App {
  text-align: center;
  background-color: #ffffff;
  font-family: 'Lato', sans-serif;
}

.container{
  display: flex;
  padding: 10px 15px;
  margin-bottom: 2em;
  min-height: calc(100vh - 150px);
}

.col-1{
  background-image: url(/v9-geckos-team-04/static/media/speech_to_text.04f03d28.svg);
  flex:1 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.col-2{
  flex:1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.subTitle{
  font-weight: 400;
  font-size: 2em;
  margin: 0;
  color:#6c61f6;
}

.textarea{
  overflow: auto;
  width: 80%;
  height: 300px;
  margin: 20px auto;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 3px 5px rgba(0,0,0,0.12);
  padding: 15px;
  font-size: 14px;
}

.lang-select {
  margin: 1em auto;
  color: #39364d;
}

.lang-select select {
  margin: 10px;
  padding: 10px;
  background-color: #fff;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
  font-weight: bold;
  border-radius: 5px;
  color: #39364d;
}

.pulse {
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  -webkit-animation: pulse 2s infinite ease-in-out;
          animation: pulse 2s infinite ease-in-out;
}

@-webkit-keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .5;}
  100%  {opacity: 1;}
}

@keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .5;}
  100%  {opacity: 1;}
}

@media (max-width: 480px) {

  .col-1{
    display: none;
  }

  .col-2 {
    min-height: calc(100vh - 240px);
  }

  .container {
    flex-direction: column;
    padding-top: 0;
    margin: 0;
  }

  .title{
    font-size: 45px !important;

  }


  .lang-select {
    font-size: .9em;
  }

  .lang-select select {
    font-size: .8em;
    padding: 5px;
    margin-bottom: 5px;
  }

  .textarea{
    height: 200px;
    margin: 15px auto;
  }
  .feature-container{
    flex-direction: column;
    padding: 0 !important;
  }

  .about-para{
    padding: 20px !important;
    text-align: left;
  }
  .feature-card{
    margin: 12px 0 !important;
    padding: 10px;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
    border-radius: 6px;
  }
}

.header{
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
    color: #453adf;
    font-size: 65px;
    font-family: 'B612 Mono', monospace;
    margin:8px 0 8px 25px;
    flex: 1 1;
    text-align: left;
}

.navbar {
    margin: 1em;
    position: absolute;
    top: 0;
    right: 0;

}

.navbar ul {
    padding: 0;
    margin: 0;
}

.navbar li{
    list-style-type:none;
    display: inline-block;
    margin: 0 6px;
    padding-bottom: 3px;
    /* border-bottom: 2px solid transparent;
    transition: all 300ms ease; */
    border-bottom: 2px solid #FF4242;
}

.navbar li a{
    text-decoration: none;
    color:#FF4242;
    font-size: 18px;
}

.navbar li:hover {
    border-bottom: 2px solid #FF4242;
}


@media (max-width: 480px) {

  .navbar {
      position: static;
      margin: .5em;

  }
  .subTitle {
      font-size: 1.5em;
      margin: 0;
  }
}

.footer{
    padding:8px;
    color: #ffffff;
    background-color: #4c4c56;
    font-weight: 300;


  }

  .creators{
    color:#ffffff;
  }
  .profiles {
    display: flex;
    justify-content: center;
    /* width: 35vw; */
    margin: 0 auto;
  }
.profiles a{
  color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
}
.profiles a:hover{
  color: #ffffff;
  text-decoration: underline;
  font-weight: 700;
}
.profile{

  display: flex;
  align-items: center;
}
.hemlata{
  margin-left: 40px;
}
  .footer p{
      margin:5px;
  }
  .icon{
      margin:3px;
  }
  .copyright{
    color: rgba(255, 255, 255, 0.7);
    font-size: 13px;
  }
.repo{
  color: rgba(255, 255, 255, 0.7);
}
.repo:hover{
  color:#ffffff;

}

@media (max-width: 480px) {
  .profiles {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .profile {
    margin: 0;
  }
  .hemlata {
    order: -1;
  }
}

.btn {
  padding: 10px;
  background-color: #39364d;
  color: #ffffff;
  font-size: 14px;
  border-radius: 5px;
  font-weight: bold;
  margin: 0 5px 20px 5px;
  -webkit-transition: all 100ms ease;
  transition: all 100ms ease;
}

.btn:hover, .btn:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.btn:active {
  background-color: #ffffff;
  color: #2c274b
}

.start, .stop {
  padding: 10px;
  background-color: #6c61f6;
  color: #ffffff;
  font-weight: bold;
  font-size: 14px;
  border-radius: 5px;
  -webkit-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.start:hover, .stop:hover, .start:focus, .stop:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.about-us {
    padding: 40px;
    padding-top: 1em;
}

h1{
    color:#3f3c56;
    font-weight: lighter;
    font-size: 24px;
}

.about-para{
    font-size: 18px;
    font-family: Helvetica;
    padding: 30px 120px;
    line-height: 1.5;
    text-align: left;
}

.logo {
  text-align: center;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo svg {
  cursor: pointer;
  -webkit-animation: spin 2s infinite;
          animation: spin 2s infinite;
}

@-webkit-keyframes spin {
  0% {-webkit-transform: scale(1);transform: scale(1);}
  10% {-webkit-transform: scale(.9);transform: scale(.9);}
  20% {-webkit-transform: scale(1);transform: scale(1);}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes spin {
  0% {-webkit-transform: scale(1);transform: scale(1);}
  10% {-webkit-transform: scale(.9);transform: scale(.9);}
  20% {-webkit-transform: scale(1);transform: scale(1);}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}

.features {
    background-color: #f2f2f2;
    padding: 40px;
    padding-top: 1em;
}
.feature-container {
    display: flex;
    padding: 20px;
}

.feature-card {
    flex:1 1;
    margin: 0 26px;
}

img {
    height:50px;
    width:50px;
}
h2{
    color:#453adf;
}
p{
    font-size: 1.1em;
    line-height: 1.5;
}

@media (max-width: 480px) {
  .feature-container {
    flex-direction: column;
  }
  .feature-card {
    margin-bottom: 1.5em;
  }
  .feature-card h2 {
    margin: .5rem auto;
  }
}

.Share {
  display: flex;
  flex-direction: column;
}

.Share label {
  font-size: 1.2em;
  margin-bottom: .5em;
}

.share-btns-cont {
  display: flex;
}

.share-btn {
  margin: auto 10px; 
}

.share-label{
  line-height:2;
  color: #39364d;
  margin: auto 5px;
  opacity: .8;
  -webkit-transform: scale(.9);
          transform: scale(.9);
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  cursor: pointer;
}

.share-btn:hover, .share-btn:focus {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.share-hidden {
  visibility: hidden;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}

