.header{
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
    color: #453adf;
    font-size: 65px;
    font-family: 'B612 Mono', monospace;
    margin:8px 0 8px 25px;
    flex: 1;
    text-align: left;
}

.navbar {
    margin: 1em;
    position: absolute;
    top: 0;
    right: 0;

}

.navbar ul {
    padding: 0;
    margin: 0;
}

.navbar li{
    list-style-type:none;
    display: inline-block;
    margin: 0 6px;
    padding-bottom: 3px;
    /* border-bottom: 2px solid transparent;
    transition: all 300ms ease; */
    border-bottom: 2px solid #FF4242;
}

.navbar li a{
    text-decoration: none;
    color:#FF4242;
    font-size: 18px;
}

.navbar li:hover {
    border-bottom: 2px solid #FF4242;
}


@media (max-width: 480px) {

  .navbar {
      position: static;
      margin: .5em;

  }
  .subTitle {
      font-size: 1.5em;
      margin: 0;
  }
}
